body {
  font-family: sans-serif;
  margin: 0;
  padding: 0;
  background-color: rgb(228, 226, 226);
}

.form {
 
  display: flex;
  justify-content: center;

  padding: 10px;
  background-color: rgb(228, 226, 226);
  z-index: 1;
  box-shadow: 0px 0px 10px 5px rgb(182, 180, 180);
}
.newform {
  display: flex;
  justify-content: center;
  padding: 10px;
  width: 70%;
  margin-bottom: 5px;
  border-radius: 10px;
  background-color: rgb(228, 226, 226);
  box-shadow: 0px 0px 10px 5px rgb(182, 180, 180);
}
.NextForm {
  text-align: center;
  display: flex;
  flex-direction: column;
  width: 100%;
}
.NextForm .input {
  margin-top: 5px;
}
.input {
  padding: 10px;
  margin-left: 5px;
  border: 1px solid rgb(196, 196, 196);
  border-radius: 30px;
  box-shadow: 0px 0px 10px 5px rgb(182, 180, 180);
  outline: none;
  overflow: scroll;
}

.button {
  margin-left: 5px;
  border: 1px solid rgb(196, 196, 196);
  border-radius: 30px;
  color: rgb(107, 105, 105);
  padding: 10px;
  box-shadow: 0px 0px 10px 5px rgb(182, 180, 180);
}
.overflow {
  width: 200px;
  overflow: auto;
}
.next {
  margin-top: 5px;
}

.body {
  display: flex;
  width: 100%;
  margin-top: 10px;
  padding-top: 10px;
  justify-content: center;
  background-color: rgb(228, 226, 226);
  position: relative;
}

.post {
  border: 1px solid rgb(196, 196, 196);
  border-radius: 30px;
  background-color: white;
  margin: 10px;
  padding: 20px;
  display: flex;
  justify-content: center;
  width: 80%;
  box-shadow: 0px 0px 10px 5px rgb(182, 180, 180);
}

hr {
  color: rgb(196, 196, 196);
}

.postText {
  display: flex;
  flex-direction: column;
  width: 60%;
}

.title {
  font-size: 25px;
  display: block;
  font-weight: bold;
  text-decoration: none;
}

.postDescr {
  font-size: 20px;
  font-weight: 100;
}

.post > img {
  width: 300px;
  height: 300px;
  object-fit: cover;
  border-radius: 30px;
}

.flex {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 50%;
}

.leftside {
  width: 15%;
  border: 1px solid rgb(196, 196, 196);
  border-radius: 30px;
  background-color: white;
  height: 70vh;
  box-shadow: 0px 0px 10px 5px rgb(182, 180, 180);
  top: 70px;
  left: 10px;
}

.leftside > p,
.leftside > h2 {
  width: 50%;
  margin: 10px auto;
}

.detail {
  width: 10%;
  height: 70vh;
  overflow-y: hidden;
  border: 1px solid rgb(196, 196, 196);
  background-color: white;
  border-radius: 30px;
  padding: 10px;
  box-shadow: 0px 0px 10px 5px rgb(182, 180, 180);
  top: 70px;
  right: 10px;
}
.detail:hover {
  overflow-y: scroll;
}
.detail:hover::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 10px;
  background-color: #f5f5f5;
}

.detail:hover::-webkit-scrollbar {
  width: 8px;
  background-color: #f5f5f5;
}

.detail:hover::-webkit-scrollbar-thumb {
  border-radius: 10px;
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #bcc0c4;
}
@media screen and (max-width: 660px) {
  .form {
    flex-direction: column;
    align-items: center;
  }.newform{
    width: 100%;
  }.flex{
    width: 90%;
  }
  .input {
    /* max-width: 90%; */
    margin-top: 5px;
  }.postText{width: 90%;}
  .form .button {
    margin-top: 5px;
  }.body{
    flex-direction: column-reverse;
    justify-content: center;
    align-items: center;
  }.b_bottom{
    border-bottom: 2px solid #7c7f81;

  }
}
